import Joi from 'joi';

const phoneRegex = /^[\+]?[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{4,7}$/gm;

const phone = Joi.string().regex(phoneRegex)
	.options({
		language: {
			string: {
				regex: {
					base: 'must be a valid phone number'
				}
			}
		}
	});

const employer = {
	business_name: Joi.string().min(2).max(24).required(),
	phone,
	email: Joi.string().email().required(),
	address: Joi.string().min(2).max(24).required(),
	city: Joi.string().min(2).max(24).required()
};

const auth = {
	create: {
		fname: Joi.string().min(2).max(24).required(),
		lname: Joi.string().min(2).max(24).required(),
		email: Joi.string().email().required(),
		password: Joi.string().min(7).required(),
		phone: phone.required()
	},
	login: {
		email: Joi.string().email().required(),
		password: Joi.string().min(7).required()
	},
	reset: {
		email: Joi.string().email().required()
	},
	passwordChange: {
		password: Joi.string().min(7).required(),
		confirm_password: Joi.string().min(7).required(),
		token: Joi.string().min(4).required()
	},
	employer: {
		fname: Joi.string().min(2).max(24),
		lname: Joi.string().min(2).max(24),
		email: Joi.string().email(),
		password: Joi.string().min(4),
		phone,
		employer
	}
};

export default auth;

